<template>
  <div class="product">
    <div class="shop" v-for="(allitem,shopIndex) in all" :key="allitem.id">
      <div><img :src="allitem.imgSrc"  alt=""></div>
      <!-- <img src="../../assets/shop/img_01-1.png" alt=""> -->

      <div>
        <p>{{ allitem.name}}</p>
        <div class="color"><span v-for="(color,colorIndex) in allitem.colors" :key="color" @click="selectColor(allitem,shopIndex,color,colorIndex)" :class="{active:allitem.cn==colorIndex}">{{ color }}</span></div>
      </div>
      <div>
        <span>{{ allitem.price}}元</span>
      </div>
      <div>
        <span @click="changeNum(allitem, -1)">-</span>
				<strong>{{ allitem.selectNum}}</strong>
				<span @click="changeNum(allitem,+1)">+</span>
      </div>
      <div>
        <!-- <button type="button" >加入购物车</button> -->
        <el-button type="danger" @click="addShop(allitem)">加入购物车</el-button>
      </div>
    </div>
  </div>

</template>

<script>
export default{
  props:{
    all:Array
  },
  data(){
    return{
      select:[]
    }
  },
  methods:{
    changeNum(shop,selectNum){
      if(shop.selectNum+selectNum<0){
        shop.selectNum=0;
      }else{
        shop.selectNum+=selectNum;
      }
      //更新当前商品的总价
      shop.totalPrice=shop.selectNum*shop.price;
      console.log(shop.totalPrice);

      // 如果减到0,需要把数据从已选商品列表里删除
      let n=this.select.findIndex(item=>item.selectNum==0);
      if(n!=-1){
          // 这个条件 成立说明数据找到了，就是0
          this.select.splice(n,1);

      }
    },
    addShop(shop){
      if(!shop.selectNum){
        this.$message.warning('请选择数量');
        return;
      }
      let data=this.select.find(item=>item.id==shop.id);
      data?shop:this.select.push(shop);
      this.$emit('addlist',this.select);

    },
    selectColor(shop,shopIndex,color,colorIndex){
      this.$set(shop,'cn',colorIndex);
      shop.imgSrc=require(`../../assets/shop/img_0${shopIndex+1}-${colorIndex+1}.png`);
      shop.selectColor=color;
    }


  }
}
</script>

<style lang="less" scoped>
.product{
  width: 1200px;
  .shop{
    width: 758px; height: 112px; margin: 0 auto;
    border: 1px salmon solid;
    display: flex;
    div:nth-child(1){
      flex: 1; padding: 26px;
      display:flex; justify-content: center; align-items: center;
      img{
        background: #eee;
      }
    }
    div:nth-child(2){
      flex:3;
      display:flex; justify-content: center; align-items: center;
      flex-direction:column;
      p{
        font-weight: bolder;
      }
      .color{
        flex-direction:row;
        span{
          display: inline-block;  border: 1px solid #FFECEC; padding: 4px 6px;
          margin: 4px; cursor: pointer;
        }
        .active{
          border:2px solid #b4a078;
          line-height: 24px;   background: url(../../assets/shop/ico_02.gif) no-repeat right bottom;
        }
      }
    }
    div:nth-child(3){
      flex:1;
      display:flex; justify-content: center; align-items: center;
      span{ color:red;}
    }
    div:nth-child(4){
      flex:1;
      display:flex; justify-content: center; align-items: center;
      span,:nth-child(2){
        width: 30px; height: 24px;
        border-top: 1px solid #ddd;      border-bottom: 1px solid #ddd;
        text-align: center;        // vertical-align: middle;
        display: inline-block;
        cursor: pointer;
      }
      span:nth-child(1){
        border-left: 1px solid #ddd;
      }
      :nth-child(2){
        width: 58px;border-left: 1px solid #ddd; border-right: 1px solid #ddd; padding: 2px;
      }
      span:nth-child(3){
        border-right: 1px solid #ddd;
      }


    }
    div:nth-child(5){
      flex:1;
      display:flex; justify-content: center; align-items: center;
      padding: 10px;
    }
  }

}
</style>
