<template>
  <div>
      <Shopping  class="shopping" :all="all"  @addlist="changeList"/>
      <ShoppingS :select="select" @del="select.splice($event,1)"/>
      <el-tag type="success" class="total-price"  effect="dark" size="medium">总价{{allPrice}}元</el-tag>
  </div>
</template>

<script>
import ShoppingAll from '../components/ShoppingCart/ShoppingAll.vue'
import ShoppingSelect from '../components/ShoppingCart/ShoppingSelect.vue'
export default{
  data(){
    return{
      all: [
        {id: 0,name: '打不翻的吸盘碗',price: 21,imgSrc: require('../assets/shop/img_01-1.png'),colors: ['粉色', '蓝色', '黄色', '绿色'],},
        {id: 1,name: '韩式帆布电脑双肩包',price: 227,imgSrc: require('../assets/shop/img_02-1.png'),colors: ['黑色', '青色', '蓝色'],},
        {id: 2,name: 'Carat钻石汤锅24cm',price: 78,imgSrc: require('../assets/shop/img_03-1.png'),colors: ['绿色', '黄色', '银色'],},
        {id: 3,name: '中筒皮毛一体雪地靴',price: 359,imgSrc: require('../assets/shop/img_04-1.png'),colors: ['栗色', '蓝色', '紫色', '红色'],},
      ],
      select: [
        // {
        //   id: 0,
        //   name: '打不翻的吸盘碗',
        //   price: 21,
        //   imgSrc: require('../assets/shop/img_03-1.png'),
        //   selectNum: 0,
        //   selectColor: '粉色',
        //   totalPrice: 0,
        // }
      ]
    }
  },
  components:{
    Shopping:ShoppingAll,
    ShoppingS:ShoppingSelect

  },
  mounted() {
    // 遍历数组
    this.$data.all.map(item=>{
      // 用来更新对象的数据，接收三条数据，（你要修改哪条数据，）
      this.$set(item,'selectNum',0);
      this.$set(item,'selectColor',item.colors[0]);
      this.$set(item,'totalPrice',0);
    })
    console.log(this.$data);
  },
  methods:{
    changeList(selectData){
       this.select=selectData;
        this.$message.success('添加购物车成功');
    }
  },
  computed:{
    allPrice(){
      let total=0;
      this.select.forEach(item=>{
        total+=item.totalPrice;
      })
      return total;
    }
  }


}
</script>

<style lang="less" scoped>
.shopping{
  margin-bottom: 20px;
}
.total-price{
  position: absolute; right: 6%; top:20%;
}
</style>
